export default {
	data() {
		this.page = 1
		return {
			logList: [],
			earn: {}
		};
	},
	onLoad() {
		this.getEarn()
		this.getLog()
	},
	onReachBottom() {
		this.page++
		this.getLog()
	},
	methods: {
		getEarn() {
			this.$app.request('lottery/dayEarn', {}, res => {
				this.earn = res.data
			})
		},
		getLog() {
			this.$app.request('lottery/log', {
				page: this.page,
			}, res => {
				if (this.page == 1) {
					this.logList = res.data
				} else {
					this.logList = this.logList.concat(res.data)
				}
			})
		},
	}
}
